body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button,article {margin:0;padding:0;font-family:'Malgun Gothic',sans-serif;line-height:1;-webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box;font-size: 14px;line-height: 1;letter-spacing: -.05em;}
.App{max-width: 360px;min-height: 100vh;margin:0 auto;padding-bottom: 100px; background-color: #282c37;}
.top-section {padding:22px 12.5px;background-color: #2c9eff;}
.flex-box{display: flex;}
.flex-block{flex: 0 1 auto;}
.top-logo {text-align: center;}
.left-menu-section .MuiButtonBase-root.MuiTab-root {background-color: #fff; color:#282c37;}
.tab-menu .MuiButtonBase-root.MuiTab-root.Mui-selected {background-color: #282c37;color: #fff;}
.tab-menu-button{display: flex;}
.tab-menu-button .MuiButtonBase-root {flex:1 1 auto;min-width: 33%;}
.left-menu-section .MuiTabs-indicator{display: none;}
.tab-panel{padding-top: 30px;}
.tab-panel .MuiBox-root{padding:0;}
.tab-panel .MuiAccordionSummary-expandIconWrapper {color: rgba(255, 255, 255, 0.8)}
.menu-accordion .MuiPaper-elevation.MuiPaper-root{box-shadow: none;background-color: #282c37;}
.menu-accordion .MuiPaper-root.MuiAccordion-root:before {height: 0;}
.dropdown-title{padding-left: 40px; cursor: pointer;    font-size: 15px;    font-weight: 700;    line-height: 50px;    color: #c2c9d6;}
.menu-accordion .MuiAccordionSummary-content,
.menu-accordion .MuiButtonBase-root.Mui-expanded,
.menu-accordion .MuiAccordionSummary-content.Mui-expanded {min-height: auto;margin:0;}
.menu-accordion .MuiAccordionDetails-root{padding:0 0;}
.menu-accordion .MuiAccordionDetails-root dd + dt {margin-top: 20px;}
.menu-accordion .MuiAccordionDetails-root dd a,
.menu-accordion dl dd a {cursor: pointer;text-decoration: none; color:#c2c9d6}
.menu-accordion .flex-box{align-items: center;}
.menu-accordion .flex-box a,
.menu-accordion .flex-box .MuiToggleButtonGroup-root{flex: 0 1 auto;}
.menu-accordion .flex-box .MuiToggleButtonGroup-root {margin-left:auto;}
.menu-accordion .flex-box button{width:34px;height:19px; padding-right: 15px; border:none;cursor:pointer;background-color:transparent;background-image:url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/star.png);background-repeat:no-repeat;background-position:calc(100% - 15px) 4px;text-indent:-10000px;}
.menu-accordion .flex-box button.Mui-selected {background-image:url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/star_on.png);
}
.menu-accordion .flex-box a span{color: #2c9eff;}
.menu-accordion>.MuiPaper-root{background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-option-g.png);background-repeat: no-repeat;background-position: 15px 13px;}
.menu-accordion>.MuiPaper-root:nth-child(2){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-box-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(3){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-deli-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(4){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-cal-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(5){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-board.png);}
.menu-accordion>.MuiPaper-root:nth-child(6){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-cychro-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(7){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-full-copy.png);}
.menu-accordion>.MuiPaper-root:nth-child(8){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-indi-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(9){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-event-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(10){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-s-shop-copy.png);}
.menu-accordion>.MuiPaper-root:nth-child(11){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/icn-stats-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(12){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/left_icn-live-g.png);}
.menu-accordion>.MuiPaper-root:nth-child(13){background-image: url(https://admin.sellerhub.co.kr/shop/admin_provider/frame/img/left_icn-payment.png);}
.login-block{margin-top: 10px; padding-bottom: 5px; justify-content: center;border-bottom: 1px solid #fff;}
.login-block .flex-block{padding:0 10px;}
.login-block .flex-block a{text-decoration: none;}
.login-block .flex-block:first-child{border-right: 1px solid #fff;}
.top-plan-text, .top-cs-alert div{padding-top: 10px; font-size: 12px;line-height: 18px; color:#fff;}
.top-plan-text span {color:yellow}
.MuiButtonBase-root.MuiButton-root.top-service-button{display:block;width:100%;height:30px;margin-top:5px;font-size:12px;line-height:20px;border:1px solid #282c37;border-radius:0;color:#282c37;background-color:#fff}
.top-cs-alert span{display:inline-block;width:30px;margin-left:5px;border-radius:8px;background-color:#fff72c;font-weight: 700; line-height:17px;text-align:center; color:#333;}
.top-cs-alert .flex-block {padding-right: 15px;}
.top-select-box .MuiInputBase-root.MuiOutlinedInput-root{ background-color: #fff; font-size: 12px; line-height: 30px;color: #979797;}
.top-select-box .MuiSelect-select.MuiInputBase-input {    min-height: 30px;    padding: 0 0; text-indent: 10px;}
.top-select-box.MuiPaper-root {height: 30px;margin-top: 10px; box-shadow: none;}
.top-select-box.MuiPaper-elevation .MuiInputBase-input {padding:0 0;
    font-family: 'Malgun Gothic', sans-serif;  font-size: 12px; font-weight: 700;}
.top-select-box.MuiPaper-elevation .MuiInputBase-input:focus{color:#979797    }